"use strict";

/*
    !!!   .
       - .
           ,
            
            use_one -     1 . (  ,  ), false  
            def_hide -    , true  
            resize_upd -        , true  
      init()

         .
    html     ...

    <div class="js-addon_spoiler"> 
        <div class="addon_spoiler_heder" tabindex="0">
            <p></p>
            <div class="addon_spoiler_indicator"></div>
        </div> 
        <div class="addon_spoiler_body">
        </div>
    </div>

       .
       addon_spoiler_heder   addon_spoiler_body
*/

import "../../styles/less/libs/addon_spoiler.less";

interface IConstructorInput {
    use_one?: boolean;
    def_hide?: boolean;
    resize_upd?: boolean;
}

class Addon_spoiler {
    public addon_spoiler_use_one: boolean; //    ,   
    public addon_spoiler_def_hide: boolean; //     
    public addon_spoiler_resize_upd: boolean; //         
    private _get_addon_spoilers: HTMLCollectionOf<Element>; //    _evt_lstr_spoiler_click2 

    constructor({ use_one = false, def_hide = true, resize_upd = true }: IConstructorInput = {}) {
        this.addon_spoiler_use_one = use_one; //     
        this.addon_spoiler_def_hide = def_hide;
        this.addon_spoiler_resize_upd = resize_upd;
        this._get_addon_spoilers = document.getElementsByClassName("js-addon_spoiler"); //     
    }

    public init(): void {
        let body_element: HTMLBodyElement | null = document.querySelector("body");

        if (body_element) {
            body_element.addEventListener("click", this._evt_lstr_spoiler_click.bind(this) as EventListener); //    " "  body
            body_element.addEventListener("keydown", this._evt_lstr_spoiler_keydown.bind(this)); //    " enter,"  body
        }

        //if (this.addon_spoiler_resize_upd && this._is_object_valid(this._get_addon_spoilers)) {
        if (this.addon_spoiler_resize_upd) {
            //             
            document.addEventListener("DOMContentLoaded", this._evt_lstr_dom_load.bind(this), { once: true }); //    " "  DOM
        }

        if (!this.addon_spoiler_def_hide && this._is_object_valid(this._get_addon_spoilers)) {
            //          
            for (let elem of this._get_addon_spoilers as unknown as HTMLElement[]) {
                //    
                this._addon_spoiler_use(elem); //      
            }
        }
    }

    // ,      ,
    //   ,    300     
    //  ,     .
    private _decoratorOneUpdRuntime<T extends Function>(func: T): () => void {
        let timer_started: boolean = false;
        let timer_id: number | null = null;
        let second_args: any[] = [];

        function caller(context: Addon_spoiler): void {
            func.bind(context)(...second_args);
            timer_started = false;
            timer_id = null;
            second_args = [];
        }

        return (...args: any[]): void => {
            second_args = args;

            if (!timer_started) {
                timer_started = true;
            } else {
                clearTimeout(timer_id as number);
            }

            timer_id = Number(setTimeout(caller.bind(null, this), 300));
        };
    }

    ////////////////////////////////////////////

    private _is_object_valid(obj: HTMLCollectionOf<Element>): boolean {
        return obj.length != 0 ? true : false;
    }

    private _evt_lstr_dom_load(evt: Event) {
        //   
        window.addEventListener("resize", this._evt_lstr_window_resize.bind(this)); //       
        this._resize_upd = this._decoratorOneUpdRuntime(this._resize_upd); //      ,  _resize_upd   .
    }

    private _evt_lstr_spoiler_click(evt: PointerEvent): void {
        //   
        let object: HTMLElement | null = (evt.target as HTMLBodyElement).closest(".addon_spoiler_heder"); //   ,    addon_spoiler_heder
        if (object) {
            //  
            evt.stopPropagation(); //   
            this._addon_spoiler_on_click(object); //      addon_spoiler_heder
        }
    }

    private _evt_lstr_spoiler_keydown(evt: KeyboardEvent): void {
        //    
        let object: HTMLElement | null = (evt.target as HTMLBodyElement).closest(".addon_spoiler_heder");
        if (object) {
            evt.stopPropagation();
            if (evt.code == "Enter") this._addon_spoiler_on_click(object);
        }
    }

    private _evt_lstr_window_resize(evt: Event): void {
        this._resize_upd();
    }

    private _resize_upd(): void {
        let get_addon_spoiler_active: HTMLCollectionOf<Element> = document.getElementsByClassName("js-addon_spoiler--active"); //    
        if (get_addon_spoiler_active.length != 0) {
            //    
            for (let i = 0; i < get_addon_spoiler_active.length; i++) {
                //    
                let this_content: HTMLElement = get_addon_spoiler_active[i].lastElementChild as HTMLElement; //   
                let this_content_height: number = this_content.scrollHeight; //   
                this_content.style.maxHeight = this_content_height + "px"; //        
            }
        }
    }

    private _addon_spoiler_on_click(DOM_obj: HTMLElement): void {
        //   addon_spoiler_heder
        let get_addon_spoilers_active: HTMLCollectionOf<Element> = document.getElementsByClassName("js-addon_spoiler--active"); //     ( )
        let spoiler_clicked: HTMLElement = DOM_obj.parentElement as HTMLElement; //    addon_spoiler_heder ( )

        if (this.addon_spoiler_use_one) {
            //     1 
            for (let i = get_addon_spoilers_active.length - 1; i != -1; i--) {
                //    ,    
                if (
                    get_addon_spoilers_active[i] !== spoiler_clicked && //         
                    !this._check_all_parent_spoiler(spoiler_clicked, get_addon_spoilers_active[i] as HTMLElement)
                ) {
                    //      
                    this._addon_spoiler_use(get_addon_spoilers_active[i] as HTMLElement); //     ()
                }
            }
        }
        this._addon_spoiler_use(spoiler_clicked); //    
    }

    private _addon_spoiler_use(DOM_obj: HTMLElement): void {
        //let is_active: boolean = DOM_obj.classList.contains("js-addon_spoiler--active") ? true : false; //      ,      
        let is_active: boolean = DOM_obj.classList.contains("js-addon_spoiler--active");
        let object_body: HTMLElement = DOM_obj.lastElementChild as HTMLElement; //    
        let object_body_height: number = object_body.scrollHeight; //   ,  
        DOM_obj.classList.toggle("js-addon_spoiler"); //       ,    
        DOM_obj.classList.toggle("js-addon_spoiler--active"); //       ,    

        if (!is_active) {
            //   
            object_body.style.maxHeight = object_body_height + "px"; //    ,   
            this._upd_parent_height(DOM_obj, object_body_height);
        } else {
            //   
            object_body.style.maxHeight = "0px"; //     0
            this._upd_parent_height(DOM_obj, 0);
        }
    }

    private _check_parent_spoiler(obj: HTMLElement): false | HTMLElement {
        //  ,     .js-addon_spoiler--active,  false
        let parent_slider: Element | null = obj.closest(".js-addon_spoiler--active");
        if (!parent_slider) {
            return false;
        }
        return parent_slider as HTMLElement;
    }

    private _check_all_parent_spoiler(child: HTMLElement, parent: HTMLElement): boolean {
        //  true  child   parent (  )(   )
        let current_child: HTMLElement = child.parentElement as HTMLElement;
        let obj_target: false | HTMLElement = this._check_parent_spoiler(current_child);

        if (obj_target) {
            if (obj_target == parent) {
                return true;
            } else {
                return this._check_all_parent_spoiler(obj_target, parent);
            }
        } else {
            return false;
        }
    }

    private _upd_parent_height(DOM_obj: HTMLElement, old_size: number): void {
        while (this._check_parent_spoiler(DOM_obj.parentElement as HTMLElement)) {
            DOM_obj = this._check_parent_spoiler(DOM_obj.parentElement as HTMLElement) as HTMLElement;
            (DOM_obj.lastElementChild as HTMLElement).style.maxHeight =
                (DOM_obj.lastElementChild as HTMLElement).scrollHeight + old_size + "px";
            old_size = (DOM_obj.lastElementChild as HTMLElement).scrollHeight + old_size;
        }
    }
}

export { Addon_spoiler };
export type { IConstructorInput };
